body{
  margin: 0;
  background-color: white;
  font-family: 'Roboto', sans-serif;
}

.shop-ui-admin.navbar{
  height: 70px;
  background-color: white;
  box-shadow: 0 2px 4px rgba(3,27,78,.1);
  display: flex;
  align-items: center;
  padding: 0 25px;
  margin-bottom: 60px;
}

.shop-ui-admin.navbar .logo{
  color: #0069ff;
  font-weight: bold;
}

.shop-ui-admin.card{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 250px;
  height: 250px;
  background-color: white;
  box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
  border-radius: 3px;
  margin-bottom: 25px;
}

.shop-ui-admin.card-big{
  width: 585px;
  box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
  border-radius: 3px;
  background-color: white;
  padding: 25px;
}

.shop-ui-admin.card h2{
  margin-bottom: 10px;
  font-size: 28px;
}

.shop-ui-admin.card p{
  margin-top: 0;
  color: #999;
}


.shop-ui-admin.cards-group{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 80px !important;
}

@media (min-width: 1400px){
  .shop-ui-admin.cards-group{
    width: 1400px;
    margin: auto;
  }
}

@media (max-width: 1400px){
  .shop-ui-admin.card{
    margin: 15px;
  }
}

@media (max-width: 780px){
  .shop-ui-admin.card, .shop-ui-admin.card-big{
    width: 90%
  }
  .shop-ui-admin.cards-group{
    align-items: center;
    flex-direction: column;
  }
}
