body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    background: white;

}

.faded{
  background-color: rgba(0,0,0,0.20) !important;
  transition: 0.3s;
}

.d--none{
  display: none;
}

.shop-ui.header--normal{
  letter-spacing: -.3px;
  font-weight: normal;
}

.items--wrapp{
  margin: auto;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.flex-j{
  display: flex;
  align-items:flex-start;
  align-content:flex-start;
  justify-content: space-between;

}

a{
  text-decoration: none;
}

a:visited{
  color: inherit;
}

.m-100{
  margin-top: 100px !important;
}

.m-40{
  margin-bottom: 40px;
}

/* GRADIENTS START */

 /* TODO: Замутить побольше красивых градиентов */

 .coloured-ui.grad--undefined{
   background: #e68b59;  /* fallback for old browsers */
   background: linear-gradient(to top right, #df5c67, #e68b59, #eeb357, #f3d05a);
 }

.coloured-ui.grad--1{
  background: #5050ad; /* fallback for old browsers */
  background: linear-gradient(to top right, #393a7a, #5050ad, #5a5cc5, #5e66d5);
}

.coloured-ui.grad--2{
  background: #e68b59;  /* fallback for old browsers */
  background: linear-gradient(to top right, #df5c67, #e68b59, #eeb357, #f3d05a);
}

.coloured-ui.grad--3{
  background: #97347c;  /* fallback for old browsers */
  background: linear-gradient(to top right, #c9357c, #97347c, #7e347c, #643881);
}

.coloured-ui.grad--4{
  background: #27867b;  /* fallback for old browsers */
  background: linear-gradient(to top right, #07597b, #27867b, #3aa17e, #3aa17e);
}

.coloured-ui.grad--5{
  background: #159238;  /* fallback for old browsers */
  background: linear-gradient(to left bottom, #A5CE2D, #159238, #079544);
}

.coloured-ui.grad--6{
  background: #0083B0;  /* fallback for old browsers */
  background: linear-gradient(to right, #0083B0, #00B4DB);
}

.coloured-ui.grad--6.shadowed{
  box-shadow: 0 0 30px rgba(0, 173, 213, 0.75);
}


/* GRADIENTS END */

.shop-ui.nav-main{
  height: auto;
  background: #00B4DB;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 50px 0;
  margin-bottom: 140px;
}

.shop-ui.navbar{
  display: flex;
  height: 65px;
  color:white;
  flex-direction: row;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.shop-ui.navbar.display--mini{
  height: 60px;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: white;
  box-shadow: 0 10px 16px -6px rgba(0,0,0,.08);
  z-index: 1000;
  color: black;
}

.shop-ui.navbar .centered{
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.shop-ui.navbar.display--mini .centered{
  width: 850px;
}

.shop-ui.navbar.display--undefined .centered{
  width: 750px;
}

.shop-ui.navbar.display--mini .nav-right a{
  font-size: 14px;
  position: relative;
  color: #959ba4;
  transition: 0.2s;
  margin-right: 30px;
}

.shop-ui.navbar.display--mini .nav-right a:hover{
  color: black;
  transition: 0.2s;
}

.shop-ui.navbar.display--mini .nav-right a .cart--counter{
  position: absolute;
  top:-5px;
  right: -12px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background: #ff3f68;
  color: white;
  font-size: 11px;
}


.shop-ui.item.card{
  display: inline-block;
  margin: auto;
  width:300px;
  height: 370px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 4px 16px 0px rgba(0,0,0,0.35);
  padding:35px 10px 25px 10px;
  margin-bottom: 50px;
}

.shop-ui.item--transparent.card{
  display: inline-block;
  position: relative;
  width:215px;
  height: 340px;
  margin-bottom: 50px;
}

.shop-ui.item--transparent .image-wrapper{
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: white;
  box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
  border-radius: 10px;
  margin-bottom: 10px;
  height: 176px;
  width: 175px;
  padding: 20px;
}

.block--service.fade{
  position: absolute;
  border-radius: 10px;
  z-index: 5;
  width: 100%;
  height: 100%;
  transition: 0.3s;
}

.shop-ui.item--transparent .image-wrapper:hover{
  cursor: pointer;
}

.shop-ui.item--transparent .image{
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.shop-ui.item--transparent .bottom-align{
  position: absolute;
  bottom: 15px;
}

.shop-ui.item--transparent .price{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  letter-spacing: -.3px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 600;
  display: inline-block;
  margin-right: 10px;
}

.shop-ui.item--transparent .price--past{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  text-decoration: line-through;
  color: grey;
  display: inline-block;
}

.shop-ui.item--transparent .bonus{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  letter-spacing: -.3px;
  font-size: 16px;
  color: green;
  font-weight: 600;
}

.shop-ui.cat--card{
  width: 100px;
  height: 100px;
  text-align: center;

}

.shop-ui.cat--card .image-wrapper{
  width: 64px;
  height: 64px;
  background-color: gray;
  border-radius: 50%;
  margin: auto;
}

.shop-ui.item--transparent .title{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  font-size: 15px;
}

.shop-ui.item .title{
  font-weight: bold;
  font-size: 28px;
}

.shop-ui.item .image{
  width: 300px;
  height: 205px;
}

.shop-ui.item .description{
  font-size: 17px;
  padding-left: 5px;
}

.shop-ui.item .price{
  font-weight: bold;
  font-size: 26px;
  text-align: left;
}

.shop-ui.button{
  padding: 9px 42px;
  background-color: #00aad3;
  font-size: 16px;
  font-weight: 400;
  color: white;
  border-radius: 6px;
  border: none;
  transition: 0.2s;
}

.shop-ui.button:hover{
  background-color: #0b99bb;
  cursor: pointer;
  transition: 0.2s;
}

.shop-ui.featured.card{
  display: inline-block;
  position: relative;
  width: 190px;
  height: 355px;
  border-radius: 20px;
  padding: 25px;
  color: white;
}

.shop-ui.featured-block{
  display: flex;
  justify-content: space-between;

}

.shop-ui.cards-holder{
  display: inline-flex;
  width: 240px;
  flex-direction: column;
  justify-content: space-between;
}

.shop-ui.featured.card.mini{
  height: 140px;
  width: 190px;
  text-decoration: none;
}

.shop-ui.featured.card h2{
  margin-top: 0;
}

.shop-ui.featured h2{
  font-weight: normal;
  margin-bottom: 10px;
  font-size: 28px;
}

.shop-ui.featured p{
  color: rgba(255, 255, 255, 0.86);
  font-size: 14px;
  margin-top: 0;
}

.shop-ui.abs.end-par{
  position: absolute;
  bottom: 30px;
}

/* CART START */

.shop-ui.cart--desktop{
  position: fixed;
  bottom: 60px;
  left: 60px;
  height: 80px;
  width: 80px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shop-ui.cart--desktop--counter{
  position: fixed;
  background-color: #ff3f68;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 120px;
  left: 110px;
  height: 27px;
  width: 27px;
  border-radius: 50%;
}

/* CART END */


.shop-ui.content{
  margin: auto;
  margin-top: 50px;
}


@media (min-width: 1250px){
  .shop-ui.nav-main .content--f {
      width: 750px;
      margin: auto;
  }

  .shop-ui.navbar {
    padding: 0;
  }

  .shop-ui.content{
    width: 850px;
  }

  .shop-ui.content.w-1200{
    width: 1200px;
  }
}

@media (max-width: 1250px){
  .shop-ui.cart--desktop{
    display: none;
  }

  .shop-ui.cart--desktop--counter{
    display: none;
  }
}

@media (max-width: 768px){
  .shop-ui.featured-block {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-flow: column;
  }

  .shop-ui.navbar .centered{
    width: 100%;
    padding: 20px;
  }

  .shop-ui.featured.card{
    margin-bottom: 50px;
  }
  .items--wrapp {
    justify-content: center;
    align-items: center;
  }

  .shop-ui.cat--card {
    margin-bottom: 20px;
  }

  .shop-ui.content .header--normal{
    margin-left: 20px;
    margin-bottom: 60px;
  }
}

/* TOOLTIP START */

.shop-ui.tooltip{
  background: linear-gradient(to right, #667eea, #764ba2);
  color: white;
}

.shop-ui.tooltip:after {
    border-right-color: black !important;
    background: white;
    border-radius: 1px;
}

/* END TOOLTIP */

.shop-ui.divider{
  border-top: 1px solid #ececec;
}
.shop-ui.divider.m-10{
  margin: 10px 0 10px 0;
}

.shop-ui.cart-item{
  display: flex;
  padding: 30px;
}

.shop-ui.cart-image{
  width: 120px;
  height: 120px;
}

.shop-ui.cart-container{
  display: flex;
}

.shop-ui.cart.item.title{
  margin-right: 40px;
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  font-size: 18px;
}

.shop-ui.cart-item .price{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  letter-spacing: -.3px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 600;
  display: inline-block;
  margin-right: 10px;
}

.shop-ui.cart.container{
  flex: 14 14;
}

.shop-ui.cart.info{
  margin-left: 70px;
  flex: 7 7;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
  min-height: 400px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.shop-ui.cart.info .delivery-m .column{
  margin-bottom: 11px;
}

.shop-ui.cart.info .delivery-m .column .header{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  font-weight: 600;
  font-size: 18px;
}

.shop-ui.cart.info .delivery-m .column .description{
  margin: 0;
}

.shop-ui.cart.info .total{
  font-family: pfhighway,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  letter-spacing: -.3px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 600;
  display: inline-block;
  margin-right: 10px;
}
